import { Space } from "antd";
import { useTranslation } from "react-i18next";
import { useCallback, useContext } from "react";
import { useNavigate, useSearchParams } from "react-router-dom";

import { Head } from "./components";
import classes from "./Main.module.scss";
import useMainColumns from "./MainColumns";
import { CommonInfoContext } from "context/commonInfo";
import { DownloadXls, Pagination, Select, Table } from "components/shared/AntComponents";

type Props = {};

const data = Array(5).fill({
	id: 1,
	a: "Олой сити",
	b: 1,
	c: 2,
	d: 3,
	e: 4
});

const Main = (props: Props) => {
	const { t } = useTranslation();
	const navigate = useNavigate();
	const columns = useMainColumns();
	const [search, setSearch] = useSearchParams();

	const {
		state: { page, size }
	} = useContext(CommonInfoContext);

	const onChangePagination = useCallback(
		(p: number) => {
			search.set("page", String(p - 1));
			setSearch(search);
		},
		[page, search]
	);

	return (
		<div className={classes.main}>
			<Head />

			<Table
				rowKey={"id"}
				loading={false}
				columns={columns}
				dataSource={data}
				onRow={() => {
					return {
						onClick: () => navigate("/authory/1/1/complex")
					};
				}}
				headerLeftFilter={
					<Space>
						<Select size="large" width={190} placeholder={t("Олой сити")} />
						<Select size="large" width={150} placeholder={t("Бино рақами")} />
					</Space>
				}
			/>

			<div className="table-bottom mt-30">
				<Pagination total={50} pageSize={Number(size)} current={Number(page) + 1} onChange={onChangePagination} />

				<DownloadXls className="ml-auto" />
			</div>
		</div>
	);
};

export default Main;
